body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    font-family: 'Roboto', sans-serif;
    background: linear-gradient(to right, #5A7B8C, #45606D, #27333D);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;
   
}





@-webkit-keyframes handwriting {
    0% {
        opacity: 0;
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}





@keyframes handwriting {
    0% {
        opacity: 0;
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

.header h1 {
    font-family: 'Patrick Hand', cursive;
    font-size: 32px;
    color: #ffffff;
    margin: 0;
    letter-spacing: 1px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
    
    
    -webkit-animation: handwriting 2s forwards;
    
    
            animation: handwriting 2s forwards;
    opacity: 0; 
}


.navbar {
    position: absolute;
    top: 100%;
    left: 0;
    background: rgba(255, 255, 255, 0.9); 
    display: none;
    flex-direction: column;
    align-items: center;
    width: 100%;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    box-shadow: 0px -4px 8px rgba(0, 0, 0, 0.1); 
}

.navbar a {
    font-weight: 500;
    color:#ff816e;
    padding: 10px 0;
    text-decoration: none;
    transition: color 0.3s, background 0.3s; 
    position: relative;
    width: 100%;
    text-align: center;
}

.navbar a:hover {
    color: #f7fafc;  
    background: rgba(86, 123, 140, 0.1); 
}

.navbar a:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background: #5A7B8C;  
    bottom: 0;
    left: 0;
    transition: width 0.3s;
    visibility: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
}
.navbar.active {
    display: flex;
}

.menu-button {
    display: block;
    background: none;
    border: none;
    color: #fff;
    font-size: 24px;
    cursor: pointer;
    outline: none;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease; 
}

.menu-button.active { 
    -webkit-transform: rotate(90deg); 
            transform: rotate(90deg);
}


@media (min-width: 768px) {
    .menu-button {
        display: none;
    }

    .navbar {
        position: static; 
        background: transparent; 
        display: flex;
        flex-direction: row;
        width: auto;
        margin: 0;
        border-radius: 0; 
    }

    .navbar a {
        margin: 0 15px;
        padding: 0;
    }

    .navbar.active {
        display: block;
    }
    
    

    
    
}

.footer {
    font-family: 'Roboto', sans-serif;
    background: linear-gradient(to right, #5A7B8C, #45606D, #27333D);  
    padding: 10px 0px;
    text-align: center;
    position: fixed;
    bottom: 0;
    width: 100%;
    color: #ffffff;
    box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.1);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.footer p {
    margin-bottom: 8px;
    color: #ffffff;  
}

.footer a {
    color: #ffffff;  
    text-decoration: none;
    margin: 0 8px;
    transition: color 0.3s;
}

.footer a:hover {
    color: #BFD4E7;
}

.social-icons {
    display: flex;
    justify-content: center;
    margin-top: 5px;
    padding: 5px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 5px;
}

.social-icons a {
    margin: 0 8px;
    font-size: 20px;
    color: #ffffff;  
    transition: color 0.3s;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.social-icons a:hover {
    color: #BFD4E7;
}

.home-container {
  background-size:  contain; 
  background-position: center; 
  background-repeat: no-repeat; 
  height: 100vh; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
  color: white; 
}


.home-container h1 {
  font-family: 'Playfair Display', serif;
  font-size: 3em;
  color: #ff6f61;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  -webkit-animation: fadeInDown 1s both;
          animation: fadeInDown 1s both;
}

.start-button {
  font-family: 'Comic Neue', cursive;
  font-size: 1.5em;
  padding: 10px 20px;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  background: linear-gradient(45deg, #ff816e, #ff6f61);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  color: #fff;
  cursor: pointer;
  transition: box-shadow 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, box-shadow 0.3s;
  transition: transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s;
}

.start-button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 12px 20px rgba(0, 0, 0, 0.2);
}

@-webkit-keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

/* === IMPORTS ET BASE === */


body {
  margin: 0;
  padding: 0;
  font-family: 'Patrick Hand', cursive;
  background-color: #F5F4F1;  
  color: #57534E;  
  margin-bottom: 50px;
}

/* === CONTENEURS GLOBAUX === */

.container {
  margin: 50px auto;
  max-width: 80%;
  padding: 40px;
  background-color: #E8E6E1;  
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

/* === SECTIONS DE JEUX === */

.addition-game, .multiplication-game, .subtraction-game, .division-game {
  border-radius: 20px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 20px auto;
}

.addition-game {
  background-color: #C9E2D6;
}

.multiplication-game {
  background-color: #BFD4E7;
}

.subtraction-game {
  background-color: #FFDEDE;
}

.division-game {
  background-color: #FBD4D4;
}

/* === TITLES & TEXT === */

h1 {
  font-size: 2.8em;
  color: #737373;  
  margin-bottom: 20px;
}

p {
  font-size: 18px;
  color: #737373;
  margin-top: 10px;
}

/* === INPUTS & BUTTONS === */

input {
  width: 50px;
  font-size: 18px;
  margin: 0 10px;
  padding: 8px 12px;
  border: 2px solid #A8A8A8;
  border-radius: 5px;
  background-color: #FFFFFF;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input:focus {
  border-color: #B8B8B8;
  box-shadow: 0 0 10px rgba(184, 184, 184, 0.3);
  outline: none;
}

button {
  background-color: #737373;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 18px;
  margin: 10px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #636363;
}

/* === TABLE STYLES === */

table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 15px;
  font-size: 1.1em;
  background-color: #FFF3E5;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

td, th {
  padding: 10px;
  text-align: center;
  border-bottom: 2px solid #FFC3E0;
}

th {
  background-color: #FFD3B4;
  color: #333;
}


.choice-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(45deg, #f2e9dc, #e0ddd1);
}

.choice-container h1 {
  font-family: 'Playfair Display', serif;
  font-size: 3em;
  padding: 50px 0;
  color: #ff6f61;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  -webkit-animation: fadeInDown 1s both;
          animation: fadeInDown 1s both;
}

.choice-button {
  font-family: 'Patrick Hand', cursive;
  font-size: 1.5em;
  padding: 10px 20px;
  margin-top: 20px;
  border: 2px solid #A8A8A8;  
  border-radius: 5px;
  background: linear-gradient(45deg, #ff816e, #ff6f61);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  color: #fff;
  cursor: pointer;
  transition: box-shadow 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, box-shadow 0.3s;
  transition: transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s;
}


.choice-button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 12px 20px rgba(0, 0, 0, 0.2);
}

@media only screen and (max-width: 600px) {
  .container {
    max-width: 95%;
    padding: 20px;
  }
  
  h1 {
    font-size: 2em;
  }

  p {
    font-size: 16px;
  }

  input {
    width: 100%;
    margin: 5px 0;
  }
}




@-webkit-keyframes slideIn {
  from {
      -webkit-transform: translateY(-50px);
              transform: translateY(-50px);
      opacity: 0;
  }
  to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
  }
}




@keyframes slideIn {
  from {
      -webkit-transform: translateY(-50px);
              transform: translateY(-50px);
      opacity: 0;
  }
  to {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
  }
}

.animated-entry {
  -webkit-animation: slideIn 0.5s ease-out;
          animation: slideIn 0.5s ease-out;
}

.cards-container .score-card {
  -webkit-animation: fadeIn 0.6s ease-out forwards;
          animation: fadeIn 0.6s ease-out forwards;
}

.score-card:hover {
  -webkit-transform: scale(1.05) translateY(-10px);
          transform: scale(1.05) translateY(-10px);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
  transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
}


.cards-container {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
}

.score-card {
  width: 200px;
  padding: 20px;
  background-color: #E8E6E1;
  border: none;
  border-radius: 15px;
  box-shadow: 2px 2px 6px rgba(0,0,0,0.1);
  text-align: center;
  transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out; 
}


.game-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  margin: 0 auto 10px;
  font-size: 50px; 
}


.game-icon .fa-plus-circle {
  color: #f6d365;
}

.game-icon .fa-minus-circle {
  color: #FBD4D4;
}

.game-icon .fa-times-circle {
  color: #FFDEDE;
}

.game-icon .fa-divide {
  color: #BFD4E7;
}
.score-details h2 {
  margin-top: 10px;
  font-size: 18px;
}

.score-details p {
  font-size: 16px;
  margin-top: 8px;
}

.badge {
  display: inline-block;
  padding: 5px 10px;
  background-color: gold;
  border-radius: 5px;
  margin-top: 10px;
}

.personal-best {
  border: 3px solid #FFD700; 
  box-shadow: 0 0 10px rgba(255, 215, 0, 0.6);
}

.error-message {
  color: red;
  font-weight: bold;
  margin: 10px 0;
  padding: 10px;
  border: 1px solid red;
  background-color: #ffe6e6;
  border-radius: 5px;
}


.choice-button:focus, .choice-button:active {
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
  outline: none;
}

